<template>
	<view class="rowlist">
		<text class="toggleIcon" @click="gotoColumnlist"></text>
		<swiper class="accountList" previous-margin="75rpx" next-margin="75rpx">
			<swiper-item class="createAccount">
				<view class="content" @click="gotoEdit">
					<view class="slogan">
						<text>旅行中的每一笔开支\n都有独特的意义！</text>
					</view>
					<view class="addMark">+</view>
					<view class="explain">创建一个新帐本</view>
				</view>
			</swiper-item>
			<swiper-item class="listItem" v-for="account in accountList" :key="account._id" :id="account._id" @click="operateAccount">
				<view class="accountContent">
					<image :src="account.cover" mode="aspectFill" class="accountImg" id="payments"/>
					<view class="accountTitle"><text>{{account.accountTitle}}</text></view>
					<view class="accountDate"><text>{{`${account.date.split('T')[0]}`}}</text></view>
					<view class="editAccount">
						<text class="editIcon" id="edit"></text>
					</view>
				</view>
			</swiper-item>
		</swiper>
	</view>
</template>

<script>
	import {mapState, mapActions} from 'vuex';
	export default {
		computed: {
			...mapState('publicData', ['accountList']),
		},
		methods: {
			...mapActions('rowListData', ['storeEditAccount', 'storeEditAccountIndex']),
			operateAccount(e){
				switch(e.target.id){
					case 'payments':
						this.gotoPayments();
						break;
					case 'edit':
						this.storeEditAccount(this.accountList.filter((item, index)=>{
							if(item._id === e.currentTarget.id){
								this.storeEditAccountIndex(index);
								return true;
							}
						})[0]);
						this.gotoEdit();
						break;
				}
			},
			// 跳转到列列表页面
			gotoColumnlist(){
				uni.redirectTo({
					url: '/pages/columnlist/columnlist'
				})
			},
			// 跳转到编辑页
			gotoEdit(){
				uni.redirectTo({
					url: '/pages/edit/edit'
				})
			},
			// 跳转到增添收支页面
			gotoPayments(){
				uni.redirectTo({
					url: '/pages/payments/payments'
				})
			}
		},
		data() {
			return {
				
			};
		}
	}
</script>

<style lang="scss">
	.rowlist{
		width: 100vw;
		height: 100vh;
		background-color: #3a3a3a;
		color: #fff;
		position: relative;
		overflow: hidden;
		.toggleIcon{
			position: absolute;
			width: 70rpx;
			height: 70rpx;
			left: 20rpx;
			top: 20rpx;
			background-image: url(data:image/png;base64,);
			background-size: cover;
		}
		.accountList{
			margin-top: 90rpx;
			height: 85vh;
			.createAccount{
				border-radius: 2%;
				border: 2rpx dashed #fff;
				box-sizing: border-box;
				.content{
					height: 100%;
					text-align: center;
					display: flex;
					flex-direction: column;
					justify-content: center;
					.slogan{
						color: rgba(255, 255, 255, 0.5);
					}
					.addMark{
						font-size: 120rpx;
					}
					.explain{
						font-size: larger;
					}
				}
			}
			.listItem{
				border-left: 20rpx solid transparent;
				.accountContent{
					border-radius: 2%;
					width: 100%;
					height: 100%;
					overflow: hidden;
					background-color: #fff;
					.accountImg{
						height: 70%;
					}
					.accountTitle{
						padding: 20rpx;
						font-weight: bold;
						color: #777;
					}
					.accountDate{
						padding: 20rpx;
						color: #777;
					}
					.editAccount{
						padding: 20rpx;
						display: flex;
						justify-content: flex-end;
						.editIcon{
							width: 60rpx;
							height: 60rpx;
							background-image: url(data:image/png;base64,);
							background-size: cover;
						}
					}
				}
			}
		}
	}
</style>
